//首页右侧列表页
import React, { Component } from 'react';
import timg from '../image/timg.jpeg'

function Li(props){
    function click(index){
        if(props.my.state.index===index){
            return false;
        }
        props.my.setState({
            index:index
        })
    }

    let li = props.list.map((item,index)=>
       <li key={item.title.toString()} style={{
           transform:props.my.state.index===index?'rotate(0deg)':'rotate('+Math.ceil(Math.random()*360)+'deg)',
           position:props.my.state.index===index&&'fixed',
           top:props.my.state.index===index?'20%':Math.ceil(Math.random()*600),
           left:props.my.state.index===index?'35%':Math.ceil(Math.random()*930),
           zIndex:props.my.state.index===index?'301':Math.ceil(Math.random()*300),
           background:props.my.state.index===index||index<3?'url('+timg+') no-repeat center center':'',
           backgroundColor:props.my.state.index===index&&'rgba(0, 0, 0, 0.5)',
           backgroundSize:'100% 100%',
           color:props.my.state.index===index&&'#fff',
           transition: '1s all',
           boxShadow:props.my.state.index===index&&'0 0 4px rgba(0, 0, 0, 1)'
           }}
           onClick={click.bind(this,index)}
           >
            <div>
                <h3><a href="http://localhost:3000/index#" style={{color:props.my.state.index===index&&'#fff'}}>{item.title}</a></h3>
                <span>{item.brief}</span>
                {/* <img src={timg} alt="" /> */}
            </div>
       </li>
    )

    return li;
}

class List extends Component {

    constructor(props){
        super(props);
        this.getdata = this.getdata.bind(this);
        this.state = {
            datas:[],
            index:null
        }
    }

    getdata(){
        fetch("http://127.0.0.1:1170/article",{
            method: 'GET',
            headers: {
                "Access-Control-Allow-Origin": "*",
                "Content-Type": "text/plain"
            },
        }).then(res => res.json()).then(
            data => {
                console.log(data);
                this.setState({
                    datas:data
                })
            }
        )
    }

    componentDidMount(){
        this.getdata();
    }

  render() {
    
    return (
        <ul className="drip_r_content">
            <Li list={this.state.datas} my={this}/>             
        </ul>
    );
  }
}

export default List;