import React, { Component } from 'react';
import title from '../image/t.jpg';
import con from '../image/cop.png';

let left = {
  navArr:[],
  title:'',
  msg:'',
  menuArr:[]
}

function Li(props){
  let li = props.data.map((item)=>
    <li key={item.toString()}>{item}</li> 
  )
  return li;
}

function Menu(props){

  let dom = (
    <ul className='menuList'>
      <Li data={props.data} />
    </ul>
  )
  
  if(props.my.state.menu_stu){
    return dom;
  }else{
    return '';
  }

}

class Left extends Component {

  constructor(props){
    super(props);
    this.state = {
      menu_stu:false
    }
  }

  render() {

    left.navArr = ['首页','记录','其它'];
    left.title = '点滴生活,记录精彩';
    left.msg = '最无情的不是人，而是时间';

    left.menuArr = ['首页','记录','其它'];

    let my = this;

    let click = function(e){
      e.stopPropagation();
      if(my.state.menu_stu){
        my.setState({
          menu_stu:false
        })
      }else{
        my.setState({
          menu_stu:true
        })
      }
    }

    return (
      <div className='drip_left'>
        <ul className="drip_l_nav">

            <li id="portrait">
              <img alt='' src={title} />
            </li>

            <Li data={left.navArr} />

            <li id="menu" onClick={click}>
                <Menu data={left.menuArr} my={this} />
            </li>
        </ul>
        <div className="drip_l_content">
            <h2>{left.title}<img src={con} alt="" /></h2>
            <span>{left.msg}</span>
        </div>
      </div>
    );
  }
}

export default Left;
